
import React from "react";
import "./App.css";
import axios from "axios";
import Movie from "./Movie";
import "./App.css";


class App extends React.Component {
  state = {
    isLoading: true,
    movies: [],
  };

  //json데이터를 받아오는 시간이 필요하므로 완료된후에 작동하라는 명령이 필요하다.
  //async와 await는 같이 붙어다닌다.
  // async는 ()function을 비동기하라이고
  //뭘 기다리라는건지는 await로 표시
  // const movies로 해주고 console.log(movies.data.data.movies)로 해도된다.
  getMovies = async () => {
    const {
      data: {
        data: { movies },
      },
    } = await axios.get(
      "https://yts.mx/api/v2/list_movies.json?sort_by=rating"
    );
    this.setState({ movies, isLoading: false });
    //위에처럼 movies가 로딩이 완료되면 isLoadiong을 false로 바꿔서 we are ready가 출력되게 한거임
  };

  componentDidMount() {
    // setTimeout(() =>{
    //     this.setState({isLoading : false, book:true});
    // }, 6000)
    this.getMovies();
  }
  render() {
    const { isLoading, movies } = this.state;

    return (
      <section className="container">
        {isLoading?(
        <div className="loader">
            <span className="loader__text">Loading....</span>
        </div>
        ) : (
          <div className="movies">
              {movies.map(movie => (
          
                <Movie
                  key={movie.id}
                  id={movie.id}
                  year={movie.year}
                  title={movie.title}
                  summary={movie.summary}
                  poster={movie.medium_cover_image}
                  genres={movie.genres}
                />
              ))}
              </div>
        )}
      </section>
    );
  }
}

export default App;
